import React from 'react';
import styled from 'styled-components';
import ThankyouMessage from '../Components/ThankyouMessage';
import "../font.css";

export default ({tab}) => {
    const handleSubmit = (e) => {
        // e.preventDefault();
    }
    return (
        <Background ref={tab}>
            <Container>
                <Title>CONTACT</Title>
                <Postcard>
                    <Form
                        onSubmit={handleSubmit}
                        className="gform"
                        method="POST"
                        action="https://script.google.com/macros/s/AKfycbzdHHW11UlA6Zllx6OQRgFjGMKBLlMIqpS1MGGcwXG3V4CrTGia1lndLMaG_NYjuilG/exec"
                    >
                        <FormEl>
                            <Input type="text" placeholder="Enter Your Name" id="name" name="name" required></Input>
                            <Input type="mail" placeholder="Enter Your Email" id="email" name="email" required></Input>
                            <Textarea placeholder="Enter Your Message" id="message" name="message" rows="8" required></Textarea>
                            <Button>Send</Button>
                        </FormEl>
                        <ThankyouMessage />
                    </Form>
                </Postcard>
            </Container>
        </Background>
    );
}


const Background = styled.div`
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
`;
const Container = styled.article`
    width: 1340px;
`;
const Title = styled.h2`
    font-size: 2em;
    text-align: center;
    margin-bottom: 50px;
    font-weight: 800;
`;
const Postcard = styled.section`
    width: 700px;
    height: 400px;
    margin: 0 auto;
    box-shadow: 3px 2px 5px 1px rgba(0, 0, 0, 0.3);
`;
const Form = styled.form`
    width: 100%; height: 100%;
`;
const FormEl = styled.div`
    width: 100%; height: 100%;
    display: flex;
    flex-direction: column;
    padding: 30px;
`;
const Input = styled.input`
    font-family: "Noto Sans KR", sans-serif;
    font-weight: 200;
    padding: 5px 20px;
    border: none;
    border-bottom: 1px solid lightgray;
    margin-top: 20px;
    font-size: 20px;
    &:focus{outline: none;}
`;
const Button = styled.button`
    font-family: "Noto Sans KR", sans-serif;
    font-weight: 200;
    padding: 5px 20px;
    border: none;
    border-bottom: 1px solid lightgray;
    margin-top: 20px;
    font-size: 20px;
    cursor: pointer;
    border-right: 1px solid lightgray;
    transition: 0.4s;
    &:active{transform: scale(0.99);}
    &:hover{background-color: gray; color:white;}

`;

const Textarea = styled.textarea`
    font-family: "Noto Sans KR", sans-serif;
    font-weight: 200;
    padding: 20px;
    margin-top: 10px;
    border: 1px solid lightgray;
    font-size: 20px;
    resize: none;
    &::-webkit-scrollbar{width:5px;}
    &::-webkit-scrollbar-thumb{background-color: white;}
    &::-webkit-scrollbar-track{background-color: silver;}
    &:focus{outline: none;}
`;

const AfterSending = styled.div`
    display: none;
`;