const Background = styled.div`
    width: 100%;
    padding: 100px 0;
    background-color: #f5f5f5;
`;
const Container = styled.article`
    width: 1340px;
    padding: 20px;
    margin: 0 auto;
`;
const Title = styled.h2`
    text-align: center;
    margin-bottom: 50px;
    font-size: 2em;
    font-weight: 800;
`;
const Section = styled.section`
    display: flex;
    justify-content: space-between;
`;